// import { events } from "../../../custom.config"
let events;
try {
    events = require("../../../custom.config").events;
} catch (error) {
    if (error.code === 'MODULE_NOT_FOUND') {
        events = {};
    } else {
        throw error;
    }
}
export const eventListener = {
    handleEvent,
}

async function handleEvent({
    type, //eg: kochava
    code, //event code
    data,
    object_type,//object type eg: string or object
}) {
    console.log("AAAA handleEvent type: ", type)
    console.log("AAAA handleEvent code: ", code)
    console.log("AAAA handleEvent object_type: ", object_type)
    console.log("AAAA handleEvent data: ", data)
    if(type == eventTypes.kochava) {
        let kochava = events && events.kochava ? events.kochava : null;
        if(kochava && kochava.sendEvent) {
            await kochava.sendEvent(object_type, code, data);
        }
    }
}

export const eventTypes = {
    kochava: 'KOCHAVA',
}